	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3177039314\" title=\"View 'CIMG0695' on Flickr.com\"><img src=\"http://farm4.static.flickr.com/3350/3177039314_27c588f6a5.jpg\" alt=\"CIMG0695\" border=\"0\" width=\"375\" height=\"500\" /></a></p>\n<p>If&nbsp;Christopher were an experienced fisherman, he'd tell the world he caught this crappie on Lac qui Parle Lake last weekend. But he's too honest for that. We didn't get a bite during our short time on the ice, which was cut short on account of an incoming snow storm. This fish was caught by our host the night before. Thanks to my brother's friend Gary and his family for having us out.</p>","fields":{"title":"Ice Fishing","date":"07 Jan 2009","url":"https://papascott.de/2009-01-07-ice-fishing/"}}},"pathContext":{"slug":"/2009-01-07-ice-fishing/","prev":{"fields":{"title":"The Boy is Nine","date":"28 Dec 2008","slug":"/2008-12-28-the-boy-is-nine/"}},"next":{"fields":{"title":"Male and Pale","date":"21 Jan 2009","slug":"/2009-01-21-male-and-pale/"}}}}

